* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

/* Header navingtion styling and responsiveness start */
header {
  width: 90%;
  margin: 10px auto;
}
header .container {
  position: relative;
  background-color: #3cc78f;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 3;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
header .container .mobile_link,
header .container .hamburger {
  display: none;
}
header .container .logo {
  width: 20%;
  cursor: pointer;
}
header .container .link {
  display: flex;
  justify-content: space-around;
  width: 70%;
  font-weight: bolder;
}
header .container .link nav a,
header .container .link .user-cred a {
  font-size: 18px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
header .container .link nav a:hover,
header .container .link .user-cred a:hover {
  color: black;
}
header .container .link nav {
  width: 80%;
}
header .container .link nav a {
  margin: 0 20px;
  text-decoration: none;
  color: white;
}
header .container .link .user-cred {
  width: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
header .container .link .user-cred a {
  text-decoration: none;
  color: white;
}
header .container .link .oe-active {
  color: black;
}
@media screen and (max-width: 850px) {
  header .container .link {
    display: none;
  }
  header .container .logo img {
    transform: scale(0.8);
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
  }
  header .container .hamburger {
    display: block;
    width: 70%;
    font-weight: bolder;
    font-size: 35px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    color: white;
    padding-right: 10px;
  }
  header .container .hamburger i {
    z-index: 5;
  }
  header .container .hamburger span {
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
  }
  header .container .hamburger span:hover {
    color: black;
  }
  header .container .mobile_link {
    display: none;
    position: absolute;
    right: 0;
    top: 80%;
    flex-direction: column;
    width: 100%;
    background-color: #3cc78f;
    align-items: center;
    padding: 60px 0 40px 0;
    border-radius: 0 0 15px 15px;
    -webkit-border-radius: 0 0 15px 15px;
    -moz-border-radius: 0 0 15px 15px;
    -ms-border-radius: 0 0 15px 15px;
    -o-border-radius: 0 0 15px 15px;
  }
  header .container .mobile_link a {
    transition: all 0.5s ease-in-out;
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    -ms-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    text-decoration: none;
    color: white;
    font-weight: bolder;
    padding: 10px 0;
  }
  header .container .mobile_link a:hover {
    color: black;
  }
  header .container .mobile_link .oe-active {
    color: black;
  }
  header .container .link-active {
    display: flex;
  }
}

/* Header styling ends here */
/* main section desing */
.banner {
  margin-top: -67px;
  width: 100%;
  height: 700px;
  z-index: -2;
  background-image: url("../img/banner/banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.banner .overlay {
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #3cc78f;
  opacity: 0.2;
}
.banner .info {
  position: absolute;
  top: 40%;
  left: 10%;
  z-index: 1;
}
.banner .info h1,
.banner .info h2 {
  color: white;
  margin-bottom: 25px;
  font-weight: bolder;
}
.banner .info h1 {
  font-size: 50px;
  letter-spacing: 2px;
}
.banner .info button {
  outline: none;
  padding: 10px 30px;
  font-size: 20px;
  letter-spacing: 1px;
  border: 1px solid #3cc78f;
  background-color: #3cc78f;
  color: white;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.banner .info button:hover {
  background: transparent;
}
@media screen and (max-width: 570px) {
  .banner .info h1 {
    font-size: 40px;
  }
}

/* end of main section desing */
/* department section desing */
.dept_wrap {
  width: 100%;
  margin-top: 30px;
  margin-bottom: 30px;
}
.dept_wrap h1 {
  font-size: 40px;
  letter-spacing: 1px;
  width: 100%;
  margin-bottom: 30px;
  text-align: center;
  position: relative;
}
.dept_wrap h1::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 15%;
  height: 5px;
  display: block;
  background-color: #3cc78f;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
}
.dept_wrap .department {
  padding: 20px 15px 20px 10px;
  width: 100%;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.dept_wrap .department a {
  background-color: white;
  height: 70px;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
.dept_wrap .department a:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}

/* end of department section desing */
/* Teacher  section desing */
/* end of Teacher  section desing */
/* Notice  section desing */
.notice {
  width: 98%;
  margin: 20px auto;
}
.notice h1 {
  font-size: 40px;
  letter-spacing: 1px;
  width: 100%;
  margin-bottom: 30px;
  text-align: center;
  position: relative;
}
.notice h1::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 15%;
  height: 5px;
  display: block;
  background-color: #3cc78f;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
}
.notice .notice_container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.notice .notice_container div {
  align-self: flex-start;
  width: 48%;
  min-width: 350px;
  margin: 0 auto;
  background-color: white;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.notice .notice_container div h2 {
  position: relative;
  text-align: center;
  padding: 10px 0;
}
.notice .notice_container div h2::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 20%;
  height: 5px;
  display: block;
  background-color: #3cc78f;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
}
.notice .notice_container div a {
  padding: 10px 15px 20px 15px;
  display: block;
  width: 94%;
  margin: 20px auto;
  text-decoration: none;
  color: black;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
.notice .notice_container div a span strong {
  padding-right: 10px;
}
.notice .notice_container div a:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
.notice .notice_container div:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
@media screen and (max-width: 800px) {
  .notice .notice_container div {
    width: 98%;
    margin: 20px auto;
  }
}

/* end of Notice  section desing */
/* Footer  section desing */
footer {
  width: 98%;
  margin: 20px auto;
  background-color: #3cc78f;
  color: white;
  font-weight: bold;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
footer h1 {
  text-align: center;
  font-size: 40px;
  letter-spacing: 2px;
  padding: 20px 0;
}
footer .footer_wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
footer .footer_wrapper .info_container,
footer .footer_wrapper .contact_container {
  width: 100%;
}
footer .footer_wrapper .info_container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 10px;
}
footer .footer_wrapper .info_container div {
  border: 2px solid white;
  padding: 20px;
  margin: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
footer .footer_wrapper .info_container div span {
  font-size: 60px;
  display: block;
}
footer .footer_wrapper .info_container div:hover {
  color: black;
  border: 2px solid black;
}
footer .footer_wrapper .contact_container {
  margin: 30px auto;
}
footer .footer_wrapper .contact_container form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
footer .footer_wrapper .contact_container form div {
  margin: 10px 20px;
  display: flex;
  align-items: center;
  width: 40%;
  display: flex;
  justify-content: space-between;
}
footer .footer_wrapper .contact_container form div input {
  width: 70%;
  padding: 10px 20px;
  background-color: transparent;
  outline: none;
  border: 1px solid white;
  margin-left: 20px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
footer .footer_wrapper .contact_container form div textarea {
  width: 98%;
  outline: none;
  border: 1px solid white;
  padding: 10px;
}
footer .footer_wrapper .contact_container form div input,
footer .footer_wrapper .contact_container form div textarea {
  cursor: auto;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
footer .footer_wrapper .contact_container form div input:focus,
footer .footer_wrapper .contact_container form div textarea:focus {
  border: 1px solid black;
}
@media screen and (max-width: 870px) {
  footer .footer_wrapper .contact_container form div {
    width: 60%;
  }
}
@media screen and (max-width: 560px) {
  footer .footer_wrapper .contact_container form div {
    width: 80%;
  }
}
@media screen and (max-width: 400px) {
  footer .footer_wrapper .contact_container form div {
    width: 95%;
  }
}
footer .footer_wrapper .contact_container form .textarea {
  flex-direction: column;
  align-items: flex-start;
}
footer .footer_wrapper .contact_container form .textarea label {
  margin: 0px 0 20px 0;
}
footer .footer_wrapper .contact_container form input {
  margin: 10px 20px;
  padding: 8px 40px;
  outline: none;
  border: 1px solid white;
  background-color: white;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  color: #3cc78f;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
footer .footer_wrapper .contact_container form input:hover {
  border: 1px solid black;
  color: black;
}

/* end of Footer  section desing */
