/* qustion  section desing */
.qus-container {
  width: 95%;
  margin: 20px auto;
}
.qus-container .qus_wrap {
  width: 100%;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
.qus-container .qus_card {
  align-self: start;
  justify-self: center;
  background-color: white;
  max-width: 500px;
  padding: 20px 15px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
.qus-container .qus_card:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
.qus-container .qustion {
  width: 100%;
  padding: 20px 10px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
.qus-container .qustion p {
  text-align: center;
  padding: 0 5px;
}
.qus-container .qustion:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
.qus-container form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.qus-container form .ans {
  width: 100%;
}
.qus-container form .ans_box {
  padding: 10px;
  margin: 10px 0;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
.qus-container form .ans_box:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
.qus-container form input[type=radio] {
  position: relative;
  width: 25px;
  height: 25px;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  border: 2px solid #3cc78f;
  outline: none;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.qus-container form input[type=radio]:checked::after {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  position: absolute;
  background-color: #3cc78f;
  content: "";
  display: inline-block;
  visibility: visible;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}
.qus-container form input[type=submit] {
  text-decoration: none;
  align-self: flex-end;
  outline: none;
  padding: 7px 12px;
  font-size: 12px;
  letter-spacing: 1px;
  border: 1px solid #3cc78f;
  background-color: #3cc78f;
  color: white;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.qus-container form input[type=submit]:hover {
  background: transparent;
  color: #3cc78f;
}

