  width: 98%;
  margin: 20px auto;
}
.exam_results h1 {
  font-size: 40px;
  letter-spacing: 1px;
  width: 100%;
  margin-bottom: 30px;
  text-align: center;
  position: relative;
}
.exam_results h1::after {
