  background-color: white;
}
.exam_results tbody td {
  padding-left: 10px;
}
.exam_results tbody .folder_icon a {
  margin: 0 10px 0 0;
  font-size: 25px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  text-decoration: none;
  color: black;
}
.exam_results tbody .folder_icon a:hover {
  color: #3cc78f;
}
.exam_results tbody .pass_txt {
  text-align: center;
}
.exam_results tbody .pass_txt span {
  background-color: #2ce02c;
  padding: 5px 10px;
  font-weight: bold;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
@media screen and (max-width: 768px) {
  .exam_results table,
.exam_results thead,
.exam_results tbody,
.exam_results th,
.exam_results td,
.exam_results tr {
    display: block;
  }
  .exam_results thead {
    display: none;
  }
  .exam_results tbody {
    display: flex;
    flex-direction: column;
  }
  .exam_results tbody tr {
    margin: 0 0 10rem 0;
    height: 0;
  }
  .exam_results tbody tr .pass_txt {
    text-align: left;
  }
  .exam_results tbody tr .pass_txt span {
    padding: 3px 8px;
  }
  .exam_results tbody td {
    border-bottom: 1px solid #3cc78f;
    position: relative;
    padding-left: 50%;
  }
  .exam_results tbody .pass_txt {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  .exam_results tbody td:nth-of-type(odd) {
    background-color: white;
  }
  .exam_results tbody td:before {
    position: absolute;
    top: 0;
    left: 6px;
    font-weight: bolder;
    white-space: nowrap;
  }
  .exam_results tbody td:nth-of-type(1):before {
    content: "Exam Name";
  }
  .exam_results tbody td:nth-of-type(2):before {
    content: "Student Name";
  }
  .exam_results tbody td:nth-of-type(3):before {
    content: "Full Mark";
  }
  .exam_results tbody td:nth-of-type(4):before {
    content: "Student Mark";
  }
  .exam_results tbody td:nth-of-type(5):before {
    content: "Status";
  }
}

/* end of result table  section desing */
/* post  section desing */
.oe-post-sec {
  width: 98%;
  margin: 30px auto;
}
.oe-post-sec .post_container {
  width: 98%;
  margin: 0 auto;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
}
.oe-post-sec .post_wrap {
  align-self: start;
  justify-self: center;
  max-width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  background-color: white;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.oe-post-sec .post_wrap h1 {
  align-self: flex-start;
  white-space: normal;
  margin-bottom: 15px;
}
.oe-post-sec .post_wrap .post_img {
  border: 2px solid #3cc78f;
  display: block;
  width: 100%;
  height: 250px;
  margin-bottom: 20px;
}
.oe-post-sec .post_wrap .post_img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}
.oe-post-sec .post_wrap:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
.oe-post-sec .post_content {
  padding: 0 10px;
  margin-bottom: 20px;
}
.oe-post-sec .post_content p {
  white-space: normal;
}
.oe-post-sec .post_details {
  align-self: flex-start;
  display: flex;
  flex-direction: column;
}
.oe-post-sec .view_post {
  text-decoration: none;
  align-self: flex-end;
  outline: none;
  padding: 7px 12px;
  font-size: 12px;
  letter-spacing: 1px;
  border: 1px solid #3cc78f;
  background-color: #3cc78f;
  color: white;
  font-weight: bolder;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.oe-post-sec .view_post:hover {
  background: transparent;
  color: #3cc78f;
}
@media screen and (max-width: 560px) {
  .oe-post-sec .post_container {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}

/* end of post  section desing */